@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:wght@400;700&display=swap);
/* General Settings */

* {
    margin: 0;
    padding: 0;
}


html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: #fff !important;
  color: #fafaf6 !important;
  font-family: 'Ubuntu Mono', monospace !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
    height: 100vh;
    background-color: #222831 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::selection {
    background: #29a19c !important;
    color: #fafaf6 !important;
}

a {
    text-decoration: none !important;
}

input:focus, button:focus, select:focus{
    outline: none;
}

input, button, select {
    cursor: pointer;
}

.loading {
    position: relative;
    left: 45%;
    top: 40%;
    width: 10%;
}

@media (max-width: 992px)  {
    html {
        background-color: #222831;
    }

    .loading {
        position: absolute;
        left: 30%;
        top: 30%;
        width: 10%;
    }
}

/* Join Page */
.join__container {
    width: 40%;
    margin: 0 auto;
    display: grid;
    place-items: center;
}

.join__container label {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 5%;
}

.join__form {
    border: none;
    width: 60% !important;
}

.join__search {
    background-color: #29a19c !important;
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
}

.join__search:hover {
    background-color: #a3f7bf !important;
    border: 1px solid #a3f7bf !important;
    color: #393e46 !important;
}

.join__icon {
    font-size: 13rem;
    margin-bottom: 5%;
    margin-top: 30%;
    opacity: 0;
    position: relative;
    left: -100%;
    -webkit-animation: rotating-in 2.5s ease forwards;
            animation: rotating-in 2.5s ease forwards;
}

@-webkit-keyframes rotating-in {
    from {
        transform: rotate(360deg);
    }
    to {
        transform: rotate(0deg);
        left: 0;
        opacity: 1;
    }
}

@keyframes rotating-in {
    from {
        transform: rotate(360deg);
    }
    to {
        transform: rotate(0deg);
        left: 0;
        opacity: 1;
    }
}

@media (max-width: 992px)  { 
    .join__icon {
        width: 10rem;
        margin-bottom: 5%;
        margin-top: 50%;
        -webkit-animation: rotating-in 1.5s ease forwards;
                animation: rotating-in 1.5s ease forwards;
    }

    .join__container {
        width: 90%;
    }

    .join__form {
        width: 100% !important;
        margin-top: 10%;
    }
}
/* Error Page */
.error {
    text-align: center;
    display: grid;
    place-items: center;
    height: 100vh;
    padding-top: 0%;
}

.error__msg {
    margin-left: 0;
}

.error__msg h1 {
    font-size: 6rem;
    font-weight: bold;
    text-transform: uppercase;
    background-color: #29a19c;
    color: #222831;
}

.error__msg p {
    font-size: 2rem;
    padding-top: 20px;
}

.error__back {
    position: absolute;
    top: 10%;
    text-align: left;
    font-size: 1.5rem;
}

@media (max-width: 992px)  { 
    .error__back {
        position: absolute;
        top: 3%;
        text-align: left;
        font-size: 1.25rem;
    }

    .error__msg h1 {
        font-size: 4rem;
        color: #fff;
        background-color: #222831;
    }

    .error__msg p {
        font-size: 1.5rem;
        color: #29a19c;
    }
}
/* Chart Page */
.charts-center {
    display: flex;
    margin-bottom: 15%;
    width: 100% !important;
    justify-content: center;
}

@media (max-width: 992px) { 
    .charts-center {
        display: inline-block;
        margin: 0 !important;
    }
    .fusioncharts-container {
        padding-left: 0 !important;
        width: 100% !important;
    }
}
/* User Detail Page */
.user__container {
    width: 100%;
    text-align: center;
}

.user__back {
    position: absolute;
    left: 5%;
    top: 3%;
    font-size: 1.5rem;
}

.user__avatar {
    border-radius: 50%;
    border-radius: 50%;
    border: 10px solid #007bff;
    width: 30%;
    height: 30%;
    margin: 0 auto;
    margin-top: 5%;
}

.user__name {
    font-size: 3rem;
    font-weight: bold;
    margin-top: 5%;
    margin-bottom: 10px;
}

.user__username {
    margin-right: 10px;
    margin-right: 10px;
}

.user__des {
    font-size: 1.2rem;
    letter-spacing: 1.5px;
    margin-bottom: 5%;
    justify-content: center;
}

.user__info a {
    color: #fff;
}

.user__info {
    margin-bottom: 5%;
    justify-content: center;
}

.user__info div {
    margin:  0 25px;
}

.user__card {
    background: #fff;
    color: #222831;
    text-transform: uppercase;
    display: grid;
    vertical-align: middle;
    margin: 0 5%;
    border-radius: 25px;
    width: 20%;
    height: 200px;
    box-shadow: 0 10px 8px 0 rgba(0,0,0,0.2);
    margin-top: 3%;
    margin-bottom: 10%;
}

.user__card span {
    padding-top: 10px;
    color: #007bff;
    font-size: 1.2rem;
    letter-spacing: 2px;
}

.user__card p {
    font-size: 2rem;
    font-weight: bold;
}

.user__card-container {
    justify-content: center;
}

@media (max-width: 992px)  { 
    .user__back {
        position: absolute;
        left: 5%;
        top: 20px;
        font-size: 1.2rem;
    }

    .user__avatar {
        margin-top: 20%;
        width: 70%;
    }

    .user__name {
        margin-bottom: 20px;
    }

    .user__des div {
        width: 100% !important;
    }

    .user__info {
        justify-content: center;
        margin: 5% 0;
    }

    .user__info div {
        width: 100% !important;
        text-align: left;
        padding-left: 10px;
    }

    .user__card {
        width: 90%;
        margin: 5% 0;
    }
    
}
